<template>
  <el-scrollbar>
    <el-menu
      :default-active="activeMenuItem"
      class="el-menu-vertical-demo"
      @select="handleSelectItem"
    >
      <sidebar-item v-for="item in sidebarRouter" :key="item.path" :item="item" />
    </el-menu>
  </el-scrollbar>
</template>

<script setup>
import { useSidebarStore } from '@/store/sidebar'
import { useRouterStore } from '@/store/router'
import { computed } from 'vue'
import { useRouter } from 'vue-router'
import SidebarItem from '@/layout/component/sidebar/sidebarItem'

const sidebarStore = useSidebarStore()
const routerStore = useRouterStore()
const router = useRouter()

const sidebarRouter = computed(() => sidebarStore.sidebarRouter)
const activeMenuItem = computed(() => sidebarStore.activeMenuItem)

function handleSelectItem(index) {
  router.push({ name: index, params: { productId: routerStore.productId }})
}
</script>

