<template>
  <Item />
</template>

<script setup>
import { h, defineProps } from 'vue'

const props = defineProps({
  title: {
    type: String,
    default: 'default_title'
  },
  icon: {
    type: String,
    default: 'Menu'
  }
})

const Item = () => [
  h('div', { style: { marginRight: '0.5rem' }, class: ['iconfont', `icon-${props.icon}`] }),
  h('span', [`${props.title}`])
]

</script>
