<template>
  <el-sub-menu v-if="isHasChildren" :index="props.item.name">
    <template #title>
      <Item :title="props.item.meta.title" :icon="props.item.meta.icon" />
    </template>
    <el-menu-item v-for="child in props.item.children" :key="child.path" :index="child.path">
      <Item :title="child.meta.title" :icon="child.meta.icon" />
    </el-menu-item>
  </el-sub-menu>
  <el-menu-item v-else :index="props.item.name">
    <Item :title="props.item.meta.title" :icon="props.item.meta.icon" />
  </el-menu-item>
</template>

<script setup>
import { defineProps, computed } from 'vue'
import Item from '@/layout/component/sidebar/item'

const props = defineProps({
  item: {
    type: Object,
    default() {
      return {}
    }
  }
})
const isHasChildren = computed(() => props.item.meta.isGroup)

</script>

