import { defineStore } from 'pinia'
import { useRouterStore } from '@/store/router'
import { asyncRouters } from '@/router'

export const useSidebarStore = defineStore('sidebar', {
  state: () => ({
    isCollapse: false
  }),
  getters: {
    sidebarRouter: () => {
      const sidebarRouter = []

      asyncRouters.forEach(item => {
        if (item.meta.isSidebar) {
          if (!item.meta.isGroup) {
            item.children.forEach(child => {
              child.path = item.path + '/' + child.path
              sidebarRouter.push(child)
            })
          } else {
            item.children = item.children.map(child => {
              child.path = item.path + '/' + child.path
              return child
            })
            sidebarRouter.push(item)
          }
        }
      })

      return sidebarRouter
    },
    activeMenuItem() {
      const routerStore = useRouterStore()
      return routerStore.routerName
    }
  },
  actions: {
    collapseSidebar() {
      this.isCollapse = !this.isCollapse
    }
  }
})

