<template>
  <layout-header>
    <template #backIcon>
      <el-page-header @back="goBack" />
    </template>
    <template #avatar>
      <el-dropdown trigger="click" @command="handleLogOut">
        <el-avatar :src="userStore.avatarUrl" />
        <template #dropdown>
          <el-dropdown-menu>
            <li style="text-align: center; color: #8cbeff">
              <span id="text-dropdown-menu">{{ userStore.userName }}</span>
            </li>
            <el-dropdown-item command="logOut">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </template>
      </el-dropdown>
    </template>
  </layout-header>
</template>

<script setup>
import LayoutHeader from '@/components/layoutHeader'
import { useUserStore } from '@/store/user'
import { useRouter } from 'vue-router'
import { resetRouter } from '@/router'
import { ElMessage } from 'element-plus'
import { getUserInfo } from '@/apis/user'

const userStore = useUserStore()
const router = useRouter()

getUserInfo().then(res => {
  if (res.code === 200) {
    userStore.userName = res.data.userName
    userStore.avatarUrl = res.data.avatarUrl
  }
}, () => {
  ElMessage({
    type: 'error',
    message: 'error'
  })
})
function handleLogOut(command) {
  if (command === 'logOut') {
    userStore.$reset()
    userStore.resetUserToken()
    resetRouter()
    router.push('/')
    ElMessage({
      type: 'success',
      message: '退出成功'
    })
  }
}
function goBack() {
  router.push({ name: 'productList' })
}
</script>

<style scoped>
#avatar>div>span:hover {
  border: #72b2ff 2px solid;
  transition: all 300ms;
  cursor: pointer;
}
</style>
